   Base Page Styling
==================== */
body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #1a1a1a;
  background-image: url('https://www.transparenttextures.com/patterns/asfalt-dark.png');
  color: #f5f5f5;
  margin: 2rem;
}

h1 {
  text-align: center;
  color: #ff6b2d;
  text-shadow: 1px 1px 2px #3b1f0f;
  margin-bottom: 2rem;
  font-size: 2rem;
  letter-spacing: 1px;
}

/* ===================
   Layout Containers
==================== */
.tables-container {
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}

/* ===================
   Table Styling
==================== */
table {
  width: 100%;
  max-width: 450px;
  border-collapse: collapse;
  background-color: #2b2b2b;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 12px rgba(255, 69, 0, 0.25);
}

thead {
  background-color: #3b1f0f;
  color: #f9c645;
}

thead th {
  padding: 1rem;
  font-size: 0.95rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-align: center;
}

tbody tr:nth-child(odd) {
  background-color: #5c2e14;
}

tbody tr:nth-child(even) {
  background-color: #402212;
}

tbody td {
  padding: 1rem;
  text-align: center;
  border-bottom: 1px solid #2c140a;
  color: #fff3e0;
}

tfoot td {
  padding: 0;
  background-color: transparent;
}

tfoot button {
  display: block;
  width: 100%;
  padding: 1rem;
  border-radius: 0 0 10px 10px;
  font-size: 1rem;
  font-weight: bold;
  background-color: #b22222;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

tfoot button:hover {
  background-color: #e25822;
}

/* ===================
   Table Interactivity
==================== */
table#teams tbody tr:hover,
table#matches tbody tr:hover {
  background-color: #3b2416;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  box-shadow: inset 0 0 0 2px #aa4422;
}

/* ===================
   Button Styling
==================== */
button,
button.danger {
  background-color: #b22222;
  color: #fff;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  padding: 0.6rem 1.2rem;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #e25822;
}

button.danger {
  background-color: #a92b2b;
}

button.danger:hover {
  background-color: #ff3e3e;
}

/* ===================
   Dialogs
==================== */
dialog {
  background-color: #2a2a2a;
  color: #fff;
  padding: 1.5rem;
  border: none;
  border-radius: 10px;
  max-width: 700px;
  width: 100%;
  box-shadow: 0 0 20px rgba(255, 69, 0, 0.4);
}

.dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.dialog-header h3 {
  font-size: 1.25rem;
  color: #ff6b2d;
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #f5f5f5;
  cursor: pointer;
  padding: 0 0.5rem;
}

.close-btn:hover {
  color: #ffcc00;
}

#team-details-body,
#match-details-body {
  background-color: #1a1a1a;
  padding: 1rem;
  border-radius: 6px;
  font-size: 0.95rem;
  line-height: 1.5;
}

/* ===================
   Form Styling
==================== */
form label {
  display: block;
  margin-bottom: 1rem;
  font-weight: 500;
}

form input[type="text"],
form input[type="number"],
form input[type="date"],
form select {
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #555;
  background-color: #1a1a1a;
  color: #f5f5f5;
}

form input[type="checkbox"] {
  margin-left: 0.5rem;
}

form input:focus,
form select:focus {
  outline: 2px solid #ff4500;
}

.form-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem;
}

/* Autofill override */
input:-webkit-autofill {
  box-shadow: 0 0 0 1000px #1a1a1a inset;
  -webkit-text-fill-color: #f5f5f5;
  transition: background-color 9999s ease-in-out 0s;
}

/* ===================
   Match Form Layout
==================== */
.match-form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

fieldset {
  border: 1px solid #444;
  padding: 1rem;
  border-radius: 8px;
  background-color: #1c1c1c;
}

fieldset legend {
  font-weight: bold;
  color: #ffdd57;
  padding: 0 0.5rem;
}

.match-form-footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 1.5rem;
}

/* ===================
   Match Detail Blocks
==================== */
#match-details-body {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.match-details-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
  align-items: flex-start;
}

.match-team-block {
  flex: 1 1 300px;
  max-width: 500px;
  background-color: #2a1a0f;
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid #3d2b1c;
  box-sizing: border-box;
}

.match-team-block h4 {
  margin-top: 0;
  color: #ffcc00;
}

.match-team-block ul {
  margin-top: 0.5rem;
  padding-left: 1.2rem;
  color: #f0e6cc;
}

.match-team-block li {
  margin-bottom: 0.4rem;
  list-style-type: disc;
}

/* ===================
   Misc Utilities
==================== */
.label {
  font-size: 0.75rem;
  background-color: #ff6b2d;
  color: #000;
  padding: 2px 6px;
  border-radius: 4px;
  margin-left: 0.5rem;
  vertical-align: middle;
}
